ut,\nruby,\nsection,\nsummary,\ntime,\nmark,\naudio,\nvideo {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font-size: 100%;\n  font: inherit;\n  vertical-align: baseline;\n}\n\n/* HTML5 display-role reset for older browsers */\n\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block;\n}\n\nbody {\n  line-height: 1;\n}\n\nol,\nul {\n  list-style: none;\n}\n\nblockquote,\nq {\n  quotes: none;\n}\n\nblockquote:before,\nblockquote:after,\nq:before,\nq:after {\n  content: '';\n  content: none;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\n/* stylelint-enable */\n\n/** Colors */\n\n/** Box Model  */\n\n/** Background Colours */\n\nhtml {\n  font-size: 105%;\n}\n\n@media (min-width: 768px) {\n  html {\n    font-size: 95%;\n  }\n}\n\nstrong {\n  font-weight: bold;\n}\n\nem {\n  font-style: italic;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nspan,\ndiv,\np,\na,\ninput,\ntime,\ncode {\n  font-family: 'Fira Mono', monospace;\n  color: #252525;\n}\n\npre,\np,\nul {\n  margin-block-end: 1.2rem;\n}\n\nh1 {\n  margin-block-end: 0.8em;\n  font-size: 2em;\n}\n\nh2 {\n  margin-block-end: 1em;\n  font-size: 1.5em;\n}\n\nh3 {\n  margin-block-end: 1.2em;\n  font-size: 1.17em;\n}\n\nh4,\ninput {\n  margin-block-end: 1.2em;\n  font-size: 1em;\n}\n\nh5 {\n  margin-block-end: 1.2em;\n  font-size: 0.83em;\n}\n\nh6 {\n  margin-block-end: 1.2em;\n  font-size: 0.67em;\n}\n\nhtml {\n  background-color: #f0f0f0;\n  height: 100%;\n  margin: 0 !important;\n}\n\nbody {\n  margin: 0 auto;\n  padding: 0;\n  height: 100%;\n  max-width: 1280px;\n}\n\n[role=document] {\n  height: 100%;\n  display: flex;\n}\n\nmain {\n  padding: 25px;\n  overflow-y: scroll;\n  width: 100%;\n}\n\nheader.container {\n  display: block;\n}\n\n@media (min-width: 768px) {\n  header.container {\n    display: none;\n  }\n}\n\naside.sidebar {\n  display: none;\n}\n\n@media (min-width: 768px) {\n  aside.sidebar {\n    display: block;\n  }\n}\n\n/** Layouts */\n\naside.sidebar {\n  background-color: white;\n  width: 350px;\n  overflow-y: scroll;\n}\n\n.sidebar-container {\n  margin: 30px;\n}\n\n.home::before {\n  content: url(\"../images/icons8-home_filled.png\");\n  vertical-align: -25%;\n  padding-right: 8px;\n}\n\n.name-stacked {\n  margin-bottom: 30px;\n}\n\n.name-stacked h1 {\n  z-index: 5;\n  position: relative;\n  margin: 15px 0;\n  margin-top: -45px;\n}\n\n.name-stacked .grey-square {\n  height: 45px;\n  width: 45px;\n  background-color: #f0f0f0;\n}\n\n.entry-meta {\n  margin-bottom: 25px;\n}\n\n.entry-content {\n  word-break: break-word;\n}\n\n.entry-content h1,\n.entry-content h2,\n.entry-content h3,\n.entry-content h4,\n.entry-content h5,\n.entry-content h6 {\n  font-weight: bold;\n  font-family: 'Ubuntu', sans-serif;\n}\n\n.entry-content p,\n.entry-content ul,\n.entry-content ol,\n.entry-content li,\n.entry-content a {\n  font-family: 'Ubuntu', sans-serif;\n}\n\n.entry-content img.full-width {\n  width: 100%;\n}\n\n.entry-content .thumbnail img {\n  width: 100%;\n  height: auto;\n}\n\n.entry-content code {\n  background-color: rgba(98, 0, 255, 0.12);\n}\n\n.entry-content ul {\n  padding-left: 20px;\n  list-style-type: square;\n}\n\n.entry-content ol {\n  padding-left: 35px;\n  list-style-type: decimal;\n}\n\nbody#tinymce {\n  margin: 12px !important;\n}\n\nheader.section-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: baseline;\n}\n\n.initials-horizontal {\n  display: inline-block;\n}\n\n.initials-horizontal h1 {\n  z-index: 5;\n  position: relative;\n  margin: 0;\n}\n\n.initials-horizontal .grey-square {\n  height: 45px;\n  width: 45px;\n  background-color: #f0f0f0;\n  margin: 0 8px;\n  display: inline-block;\n}\n\n.initials-horizontal .grey-square:first-of-type {\n  margin-left: 0;\n}\n\n.nav-menu {\n  display: none;\n}\n\n.hamburger-menu {\n  float: right;\n  display: inline-block;\n  width: 45px;\n  height: 45px;\n  background-color: #f0f0f0;\n}\n\n.hamburger-menu div {\n  height: 95%;\n  width: 100%;\n  cursor: pointer;\n}\n\n.hamburger-menu .closed {\n  background: url(\"../images/icons8-menu.png\") no-repeat center center;\n}\n\n.hamburger-menu .open {\n  display: none;\n  background: url(\"../images/icons8-delete_sign.png\") no-repeat center center;\n}\n\nheader.container {\n  background-color: white;\n  padding: 0 25px;\n}\n\n.sidebar-section {\n  margin: 25px 0;\n}\n\n.sidebar-section h4,\n.sidebar-section input {\n  margin: 6px 0;\n}\n\n.social-icons {\n  padding-bottom: 25px;\n}\n\na:hover > h4 {\n  background-color: #f0f0f0;\n}\n\ninput[type=search] {\n  background-color: white;\n  background-image: url(\"../images/icons8-search.png\");\n  background-position: 10px 10px;\n  background-repeat: no-repeat;\n  padding-left: 40px;\n  height: 45px;\n  box-sizing: border-box;\n  border: 1px solid black;\n  width: 100%;\n}\n\ninput[type=submit] {\n  height: 45px;\n  box-sizing: border-box;\n  border: 1px solid black;\n  border-left: 0;\n  padding: 0 10px;\n  cursor: pointer;\n}\n\nform[role=search] {\n  display: flex;\n}\n\nform[role=search] label {\n  width: 100%;\n}\n\n.social-icons a {\n  text-decoration: none;\n}\n\n/** Components */\n\n.tiles {\n  display: flex;\n  flex-flow: wrap;\n  margin-bottom: 20px;\n}\n\n.tiles .tile {\n  word-break: break-word;\n}\n\n.tiles .tile .thumbnail {\n  margin-top: 20px;\n}\n\n.tiles .tile .thumbnail img {\n  width: 100%;\n  height: auto;\n}\n\n.tiles .tile-container {\n  padding: 20px;\n  background-color: white;\n  border: 1px solid #bbb;\n  margin: 0 10px;\n  margin-bottom: 20px;\n}\n\n.tiles .title {\n  margin-top: 0;\n}\n\n@media (max-width: 480px) {\n  .tiles .tile {\n    flex-basis: 100%;\n  }\n\n  .tiles .tile .tile-container {\n    margin-left: 0;\n    margin-right: 0;\n  }\n}\n\n@media (min-width: 480px) and (max-width: 1023px) {\n  .tiles .tile {\n    flex-basis: 50%;\n  }\n\n  .tiles .tile:nth-child(2n+1) .tile-container {\n    margin-left: 0;\n  }\n\n  .tiles .tile:nth-child(2n) .tile-container {\n    margin-right: 0;\n  }\n}\n\n@media (min-width: 1024px) {\n  .tiles .tile {\n    flex-basis: 33.33%;\n  }\n\n  .tiles .tile:nth-child(3n+1) .tile-container {\n    margin-left: 0;\n  }\n\n  .tiles .tile:nth-child(3n) .tile-container {\n    margin-right: 0;\n  }\n}\n\n/** Search form */\n\n/**\n * WordPress Generated Classes\n * @see http://codex.wordpress.org/CSS#WordPress_Generated_Classes\n */\n\n/** Media alignment */\n\n.alignnone {\n  margin-left: 0;\n  margin-right: 0;\n  max-width: 100%;\n  height: auto;\n}\n\n.aligncenter {\n  display: block;\n  margin: 1rem auto;\n  height: auto;\n  max-width: 100%;\n}\n\n.alignleft,\n.alignright {\n  margin-bottom: 1rem;\n  height: auto;\n  max-width: 100%;\n}\n\n@media (min-width: 30rem) {\n  .alignleft {\n    float: left;\n    margin-right: 1rem;\n  }\n\n  .alignright {\n    float: right;\n    margin-left: 1rem;\n  }\n}\n\n/** Captions */\n\n/** Text meant only for screen readers */\n\n.screen-reader-text {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  margin: -1px;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  border: 0;\n  color: #000;\n  background: #fff;\n}\n\n.alert {\n  margin-bottom: 20px;\n}\n\n/** HLJS */\n\n/* Dracula Theme v1.2.5\n *\n * https://github.com/dracula/highlightjs\n *\n * Copyright 2016-present, All rights reserved\n *\n * Code licensed under the MIT license\n *\n * @author Denis Ciccale <dciccale@gmail.com>\n * @author Zeno Rocha <hi@zenorocha.com>\n */\n\n.hljs-ln {\n  overflow: hidden;\n}\n\n.hljs-ln-line {\n  color: black;\n}\n\n.hljs-built_in,\n.hljs-selector-tag,\n.hljs-section,\n.hljs-link {\n  color: #0095b5;\n}\n\n.hljs-keyword {\n  color: #ff79c6;\n}\n\n.hljs,\n.hljs-subst {\n  color: #f8f8f2;\n}\n\n.hljs-title {\n  color: #fbb200;\n}\n\n.hljs-string,\n.hljs-meta,\n.hljs-name,\n.hljs-type,\n.hljs-attr,\n.hljs-symbol,\n.hljs-bullet,\n.hljs-addition,\n.hljs-variable,\n.hljs-template-tag,\n.hljs-template-variable {\n  color: #ff00d9;\n}\n\n.hljs-comment,\n.hljs-quote,\n.hljs-deletion {\n  color: #6272a4;\n}\n\n.hljs-keyword,\n.hljs-selector-tag,\n.hljs-literal,\n.hljs-title,\n.hljs-section,\n.hljs-doctag,\n.hljs-type,\n.hljs-name,\n.hljs-strong {\n  font-weight: bold;\n}\n\n.hljs-literal,\n.hljs-number {\n  color: #bd93f9;\n}\n\n.hljs-emphasis {\n  font-style: italic;\n}\n\n","/* stylelint-disable */\n\n/* http://meyerweb.com/eric/tools/css/reset/ \n   v2.0 | 20110126\n   License: none (public domain)\n*/\n\nhtml, body, div, span, applet, object, iframe,\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\na, abbr, acronym, address, big, cite, code,\ndel, dfn, em, img, ins, kbd, q, s, samp,\nsmall, strike, strong, sub, sup, tt, var,\nb, u, i, center,\ndl, dt, dd, ol, ul, li,\nfieldset, form, label, legend,\ntable, caption, tbody, tfoot, thead, tr, th, td,\narticle, aside, canvas, details, embed, \nfigure, figcaption, footer, header, hgroup, \nmenu, nav, output, ruby, section, summary,\ntime, mark, audio, video {\n\tmargin: 0;\n\tpadding: 0;\n\tborder: 0;\n\tfont-size: 100%;\n\tfont: inherit;\n\tvertical-align: baseline;\n}\n/* HTML5 display-role reset for older browsers */\narticle, aside, details, figcaption, figure, \nfooter, header, hgroup, menu, nav, section {\n\tdisplay: block;\n}\nbody {\n\tline-height: 1;\n}\nol, ul {\n\tlist-style: none;\n}\nblockquote, q {\n\tquotes: none;\n}\nblockquote:before, blockquote:after,\nq:before, q:after {\n\tcontent: '';\n\tcontent: none;\n}\ntable {\n\tborder-collapse: collapse;\n\tborder-spacing: 0;\n}\n\n/* stylelint-enable */\n","/** Colors */\n$brand-primary:         #27ae60;\n\n/** Box Model  */\n$spacer:                2rem;\n\n/** Background Colours */\n$background-grey:     #f0f0f0;\n","// define base font-size for rems to work\n\nhtml {\n  font-size: 105%;\n\n  @include sidebar-breakpoint {\n    font-size: 95%;\n  }\n}\n\nstrong {\n  font-weight: bold;\n}\n\nem {\n  font-style: italic;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nspan,\ndiv,\np,\na,\ninput,\ntime,\n